* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.title {
  margin: 50px;
  margin-right: 10px;
}

nav {
  display: flex;
  justify-content: space-around;
  min-height: 15vh;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
  background-color: black;
  color: white;
  border-bottom: 1px solid white;
}

nav .logo {
  font-size: 40px;
  font-weight: 700;
}

nav .nav-links {
  display: flex;
  justify-content: space-around;
  width: 40%;
  color: white;
  margin-top: 1%;
}
nav .nav-links a {
  font-size: 22px;
  font-weight: normal;
  cursor: pointer;
  color: white;
}
nav .nav-links a:hover {
  font-size: 24px;
  font-weight: bold;
  border-bottom: 1px solid white;
}

nav .burger {
  font-size: 28px;
  display: none;
  cursor: pointer;
}

@media all and (max-width: 768px) {
  nav .logo {
    display: inline-block;
    font-size: 40px;
    font-weight: 700;
    padding: 70px 60px;
  }

  nav .burger {
    display: block;
  }

  nav .nav-links {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 100vh;
    background-color: black;
    padding: 20px;
    width: 50%;
    transform: translateX(-500px);
    transition: 0.5s ease-in-out;
  }

  nav .nav-links li {
    padding: 25px;
    border-bottom: 1px solid white;
  }

  nav .nav-links li:last-of-type {
    border-bottom: 0px solid white;
  }

  nav .nav-links a:hover {
    font-size: 18px;
    font-weight: normal;
    border-bottom: 0px solid white;
  }
}
