body {
  background-color: white;
}

.bookings {
  border: 3px solid black;
  width: 50vw;
  height: 60vh;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25%;
  background-color: black;
  color: white;
  text-align: center;
  box-shadow: 5px 5px 15px 5px rgba(95, 95, 95, 0.842);
}

.bookings:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.945);
}

h1 {
  background-color: white;
  color: black;
  height: 30%;
  padding: 2%;
  margin-left: auto;
  margin-right: auto;
}

p {
  margin-top: 3%;
  margin-bottom: 5%;
}

.bookbtn {
  margin-top: 35%;
  height: 50px;
  width: 100px;
  background-color: white;
  border-radius: 5px 5px 5px 5px;
}

.bookapt {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2%;
  margin-top: 5%;
  text-align: center;
  border: 2px solid black;
  height: 200px;
  width: 500px;
  color: black;
  background-color: white;
  box-shadow: 5px 5px 15px 5px rgba(95, 95, 95, 0.842);
}

.bookapt:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.945);
}

.book {
  margin-top: 10%;
  margin-left: 10%;
}
